          { cia: 'qualitas', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'qualitas', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'qualitas', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'qualitas', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'qualitas', deductible: '3|5', coverage: 'RC' },
          // { cia: 'qualitas', deductible: '5|10', coverage: 'RC' },
          { cia: 'mapfre', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'mapfre', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'mapfre', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'mapfre', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'mapfre', deductible: '3|5', coverage: 'RC' },
          // { cia: 'mapfre', deductible: '5|10', coverage: 'RC' },
          { cia: 'chubb', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'chubb', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'chubb', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'chubb', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'chubb', deductible: '3|5', coverage: 'RC' },
          // { cia: 'chubb', deductible: '5|10', coverage: 'RC' },
          { cia: 'hdi', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'hdi', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'hdi', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'hdi', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'hdi', deductible: '3|5', coverage: 'RC' },
          // { cia: 'hdi', deductible: '5|10', coverage: 'RC' },
          { cia: 'gnp', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'gnp', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'gnp', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'gnp', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'gnp', deductible: '3|5', coverage: 'RC' },
          // { cia: 'gnp', deductible: '5|10', coverage: 'RC' },
          { cia: 'zurich', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'zurich', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'zurich', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'zurich', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'zurich', deductible: '3|5', coverage: 'RC' },
          // { cia: 'zurich', deductible: '5|10', coverage: 'RC' },
          { cia: 'ana', deductible: '3|5', coverage: 'Amplia' },
          { cia: 'ana', deductible: '5|10', coverage: 'Amplia' },
          // { cia: 'ana', deductible: '3|5', coverage: 'Limitada' },
          // { cia: 'ana', deductible: '5|10', coverage: 'Limitada' },
          // { cia: 'ana', deductible: '3|5', coverage: 'RC' },
          // { cia: 'ana', deductible: '5|10', coverage: 'RC' },
        ]
      }
    },
    validations() {
      return {
        form: {
            fullName: { required },
            // age: { required },
            email: { required },
            phone: { required },
            gender: { required },
            postcode: { required },
            paymentMethod: { required },
            selectedModel: { required },
            selectedBrand: { required },
            selectedVersion: { required },
            selectedDescription: { required }
        },
        scheduleCallForm: {
          date: { required },
          hour: { required },
        }
      }
    },
    components: {
      QuotationProcess
    },
    methods: {
      validateForm() {
        var errors = 0;

        if (this.form.selectedModel == '')
          errors++;
        if (this.form.selectedBrand == '')
          errors++;
        if (this.form.selectedVersion == '')
          errors++;
        if (this.form.selectedDescription == '')
          errors++;
        if (this.form.fullName == '')
          errors++;
        if (this.form.age == '')
          errors++;
        if (this.form.email == '')
          errors++;
        if (this.form.phone == '')
          errors++;
        if (this.form.paymentMethod == '')
          errors++;
        if (this.form.gender == '')
          errors++;

        return (errors > 0);
      },
      refreshSelects() {
        $('#model').niceSelect('update');
        $('#brand').niceSelect('update');
        $('#version').niceSelect('update');
        $('#description').niceSelect('update');
        $('#state').niceSelect('update');
        $('#city').niceSelect('update');
        $('#date').datepicker({
            dateFormat: 'dd-mm-yy',
            changeMonth: true,
            minDate: dateToday,
            beforeShowDay: function(date) { 
             var day = date.getDay(); 
             return [(day != 0), '']; 
            } 
        });
        $('#birth_date2').datepicker({
          dateFormat: 'dd-mm-yy',
          changeMonth: true,
          changeYear: true,
          yearRange: "-100:+0"
        });
        $('#birthday').datepicker({
          dateFormat: 'dd-mm-yy',
          changeMonth: true,
          changeYear: true,
          yearRange: "-100:+0"
        });
        $('#constancy_date').datepicker({
          dateFormat: 'dd-mm-yy',
          changeMonth: true,
        });
        $('#invoice_date').datepicker({
          dateFormat: 'dd-mm-yy',
          changeMonth: true,
        });
        // $('#start_date').datepicker({
        //   dateFormat: 'dd-mm-yy',
        //   changeMonth: true,
        //   minDate: dateToday,
        //   beforeShow: function() { 
        //     $(this).datepicker('option', 'maxDate', $('#end_date').val()); 
        //   }
        // });
        // $('#end_date').datepicker({
        //   dateFormat: 'dd-mm-yy',
        //   changeMonth: true,
        //   beforeShow: function() { 
        //   $(this).datepicker('option', 'minDate', $('#start_date').val()); 
        //     if ($('#start_date').val() === '') $(this).datepicker('option', 'minDate', 0);        
        //   }
        // });
        $('#beneficiary_birth_date').datepicker({
          dateFormat: 'dd-mm-yy',
          changeMonth: true,
          changeYear: true,
          yearRange: "-100:+0"
        });
      },
      onChangeTerm() {
        setTimeout(() => {
        this.refreshSelects();
            $('#term').on('change', () => {
                this.formFinancing.term = Number($('#term').val());
                // this.refreshSelects();
            });
        }, 100)
      },
      selectDate() {
        $('#date').datepicker('show');
      },
      selectBirthDay() {
        $('#birthday').datepicker('show');
      },
      getModels() {
        this.$InsuranceService.getModels().then((result) => {
          this.models = result.data.model;

          setTimeout(() => {
            this.refreshSelects();

            $('#model').on('change', () => {
              this.form.selectedModel = $('#model').val();
              $('#brand').val('');
              $('#version').val('');
              $('#description').val('');

              this.getBrands();

              setTimeout(() => {
                this.refreshSelects();
              }, 100);
            });
          }, 100);
        }, (err) => alert(err));
      },
      getBrands() {
        if (this.form.selectedModel != '') {
          const data = {
            model: this.form.selectedModel
          };

          this.$InsuranceService.getBrands(data).then((result) => {
            this.brands = result.data.brand;

            setTimeout(() => {
              this.refreshSelects();

              $('#brand').on('change', () => {
                this.form.selectedBrand = $('#brand').val();
                $('#version').val('');
                $('#description').val('');

                this.getVersions();

                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err));
        }
      },
      getVersions() {
        if (this.form.selectedBrand != '') {
          const data = {
            model: this.form.selectedModel,
            brand: this.form.selectedBrand
          };

          this.$InsuranceService.getVersions(data).then((result) => {
            this.versions = result.data.version;

            setTimeout(() => {
              this.refreshSelects();

              $('#version').on('change', () => {
                this.form.selectedVersion = $('#version').val();
                $('#description').val('');

                this.getDescriptions();
                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err));
        }
      },
      getDescriptions() {
        if (this.form.selectedVersion != '') {
          const data = {
            model: this.form.selectedModel,
            brand: this.form.selectedBrand,
            version: this.form.selectedVersion
          };

          this.$InsuranceService.getDescriptions(data).then((result) => {
            this.descriptions = result.data.description;

            setTimeout(() => {
              this.refreshSelects();

              $('#description').on('change', () => {
                this.form.selectedDescription = $('#description').val();

                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err));
        }
      },
      calculateAge(date) {
        const today = new Date();
        const birthdate = new Date(date);
        let age = today.getFullYear() - birthdate.getFullYear();
        const m = today.getMonth() - birthdate.getMonth();

        if (m < 0 || (m === 0 && today.getDate() < birthdate.getDate())) {
          age--;
        }

        return age;
      },
      saveQuotation() {
        this.isLoading = true;

        event.preventDefault();

        this.form.age = this.calculateAge(moment($('#birthday').val(), 'DD-MM-YYYY').format('YYYY/MM/DD')).toString();

        var data = {
          data: {
            full_name: this.form.fullName,
            age: this.calculateAge(moment($('#birthday').val(), 'DD-MM-YYYY').format('YYYY/MM/DD')),
            email: this.form.email,
            phone: this.form.phone,
            gender: $('#gender').val(),
            postcode: this.form.postcode,
            payment_method: $('#paymentMethod').val(),
            vehicle_model: this.form.selectedModel,
            vehicle_brand: this.form.selectedBrand,
            vehicle_version: this.form.selectedVersion,
            vehicle_description: this.form.selectedDescription
          }
        };

        var data2 = {
          birthday: moment($('#birthday').val(), 'DD-MM-YYYY').format('YYYY-MM-DD'),
		      email: this.form.email,
		      fullName: this.form.fullName,
		      gender: $('#gender').val(),
		      paymentMethod: $('#paymentMethod').val(),
		      phone: this.form.phone,
		      postcode: this.form.postcode,
		      vehicleBrand: this.form.selectedBrand,
		      vehicleDescription: this.form.selectedDescription,
		      vehicleModel: this.form.selectedModel,
          vehicleVersion: this.form.selectedVersion,
          origin: 'Web'
        }
        this.form.gender = $('#gender').val();
        this.form.paymentMethod = $('#paymentMethod').val();

        this.$InsuranceService.saveQuotation(data).then((result) => {
          
          this.$PolicyService.saveQuotation(data2).then((result1) => {
            if (result1.response) {
              this.dataContractor.id = result1.data.id;
              this.dataContractor.fullName = data2.fullName;
              this.dataContractor.age = this.form.age;
              this.dataContractor.birthDay = data2.birthday;
              this.dataContractor.email = data2.email;
              this.dataContractor.phone = data2.phone;
              this.dataContractor.gender = data2.gender;
              this.dataContractor.postcode = data2.postcode;
              this.dataContractor.paymentMethod = data2.paymentMethod;
              this.dataContractor.selectedModel = data2.vehicleModel;
              this.dataContractor.selectedBrand = data2.vehicleBrand;
              this.dataContractor.selectedVersion = data2.vehicleVersion;
              this.dataContractor.selectedDescription = data2.vehicleDescription;

              this.vehicleKey = result.data.vehicle_key;
              this.quoteInsuranceId = result.data.quoteInsuranceId;
              window.scrollTo(0, 0);
              this.getAllQuotes();
            }
          }, (err) => { 
            this.isLoading = false;
            alert(err.message);
          });
        }, (err) => {
          this.isLoading = false;
          alert(err.msg);
        });
      },
      getQuoteInsurance(data, index) {
        this.$InsuranceService.getQuote(data).then((result) => {
          if (result.data.length > 0) {
            var newData = result.data[0];

            newData.materialDamage = result.data.deductible;
            if (data.deductible == '3|5') {
              newData.material_damage = 3;
              newData.steal_total = 5;
            } else {
              newData.material_damage = 5;
              newData.steal_total = 10;
            }

            if (data.coverage == 'Amplia')
              this.quotations[0].data.push(newData);
            if (data.coverage == 'Limitada')
              this.quotations[1].data.push(newData);
            if (data.coverage == 'RC')
              this.quotations[2].data.push(newData);

            this.isLoading = false;
          }
          // else if (index == 15) {
          //   this.isLoading = false;
          //   alert('Por el momento no hay cotizaciones para este vehculo, favor de intentar despus');
          //   location.reload();
          // }
        }, (err) => {
          alert(err);
          console.log(err);
        });
      },
      getAllQuotes() {
        this.quotationsToExecute.forEach((item, index) => {
          var dataToSend = item;

          dataToSend.vehicle_key = this.vehicleKey;
          dataToSend.age = this.form.age;
          dataToSend.model = this.form.selectedModel;
          dataToSend.gender = this.form.gender;
          dataToSend.postcode = this.form.postcode;

          this.getQuoteInsurance(dataToSend, index);
        });
      },
      formatPrice(value) {
          let num = (value/1).toFixed(2);
          return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      },
      buyInsurance(dataInsurance) {
        this.isLoading = true;
        var data = {
          data: {
            id: this.quoteInsuranceId,
            cia: dataInsurance.cia,
            material_damage: dataInsurance.material_damage,
            steal_total: dataInsurance.steal_total,
            initial_payment: parseInt(dataInsurance.first_payment)
          }
        };
        this.insuranceData.id = this.dataContractor.id;
        this.insuranceData.cia = dataInsurance.cia;
        this.insuranceData.duty = dataInsurance.duty;
        this.insuranceData.iva = dataInsurance.iva;
        this.insuranceData.net_price = dataInsurance.net_price;
        this.insuranceData.payments = dataInsurance.payments;
        this.insuranceData.price_total = dataInsurance.price_total;
        this.insuranceData.recharges = dataInsurance.recharges;
        this.insuranceData.material_damage = dataInsurance.material_damage;
        this.insuranceData.steal_total = dataInsurance.steal_total;
        this.insuranceData.initial_payment = parseInt(dataInsurance.first_payment);
        
        var data2 = {
          id: this.dataContractor.id,
          duty: dataInsurance.duty,
          iva: dataInsurance.iva,
          netPrice: dataInsurance.net_price,
          priceTotal: dataInsurance.price_total,
          firstPayment: dataInsurance.first_payment,
          payments: dataInsurance.payments,
          recharges: dataInsurance.recharges,
          cia: dataInsurance.cia,
          coverage:"Amplia",
          deductible: dataInsurance.material_damage + '|' + dataInsurance.steal_total,
          budget: "0"
        };

        this.$PolicyService.newInsurance(data2).then((res) => {
          if (res.response) {
            this.buyInsuranceFlag = true;
            window.scrollTo(0, 0);
            this.$InsuranceService.quoteInsurance(data).then((result) => {
              this.isLoading = false;
            }, (err) => alert(err.msg));
          }
        }, (err) => alert(err.message));
      },
      scheduleCall() {
        this.scheduleCallFlag = true;
      },
      completeProcess() {
        this.processOnline = true; 
      },
      schedule() {
        this.scheduleCallForm.hour = $('#hour').val();
        this.scheduleCallForm.date = $('#date').val();
        if (!this.scheduleCallForm.date || !this.scheduleCallForm.hour) {
          alert('Favor de llenar los datos correspondientes para agendar la cita');
          return;
        }

        var today = moment(new Date()).format('YYYY-MM-DD');
        var actualHour = new Date().getHours();
        var minutes = new Date().getMinutes();
        var date = moment($('#date').val()).format('YYYY-DD-MM');
        var hour = $('#hour').val();

        if ( date == today ) {
          var selectedHour = hour.slice(0, 2);
          var selectedMinutes = hour.slice(3, 5);
          console.log(Number(minutes));
          if (Number(selectedHour) < actualHour ) {
            alert('Favor de elegir fecha y hora vlidas');
            return;
          } else {
            if ( Number(selectedMinutes) < Number(minutes) && Number(selectedHour) == actualHour) {
              alert('Favor de elegir fecha y hora vlidas');
              return;
            }
          }
        }

        var data = {
          data: {
            id: this.quoteInsuranceId.toString(),
            date_contact: moment($('#date').val()).format('YYYY-DD-MM'),
            hour_contact: this.scheduleCallForm.hour,
            type_contact: 'Agendar llamada'
          }
        };

        this.$InsuranceService.scheduleCall(data).then((result) => {
          console.log(result);
          alert('En breve ser contactado por un asesor');
          window.location = '/';
        }, (err) => alert(err.msg));
        
      }
    },
    
    created() {
      
      this.getModels();
    },
  }
</script>

<style lang="scss">
  .error {
    border-color: red !important;
  }

  .post_select_new {
    position: relative;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: none;
    outline: none;
    font: 400 22px/40px "Source Sans Pro", sans-serif;
    padding: 6px 25px;
    height: 55px;
    border-radius: 5px;
    color: rgba(0, 0, 0, 0.6);
    min-width: 100%;
    margin-bottom: 20px;
    // background-color: transparent;
    &:after {
      display: none;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 20px;
      font: normal normal normal 16px/40px FontAwesome;
      color: #151515;
    }
    .list {
      width: 100%;
      border-radius: 0;
      margin: 0;
      border: 0;
      box-shadow: none;
      outline: none;
      box-shadow: 1px 0px 7px 0px rgba(22, 43, 70, 0.1);
    }
  }

  .input-icons {
    width: 100%;
    margin-bottom: 0px;
    i {
      position: absolute;
      bottom: 44px;
      right: 8px;
      cursor: pointer;
    }
  }

  .nice-select .current {
      width: -webkit-fill-available !important;
      display: block;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
  }

  .border-box {
    -webkit-box-shadow: 1px 4px 3px -1px rgba(156,156,156,1);
    -moz-box-shadow: 1px 4px 3px -1px rgba(156,156,156,1);
    box-shadow: 1px 4px 3px -1px rgba(156,156,156,1);
    border-radius: 0px 0px 40px 0px;
    -moz-border-radius: 0px 0px 40px 0px;
    -webkit-border-radius: 0px 0px 40px 0px;
    border: 0px solid #000000;
  }
</style>



