<template>
  <div>
    <vue-headful title="Credimvil | Cotiza tu crdito"/>
    <menu-component></menu-component>

    <!-- Banner Area -->
    <section class="banner_area banner_area_5">
    	<div class="container">
    		<h6>Conoce el plan de crdito y solictalo en lnea</h6>
    		<h2>Cotiza tu crdito</h2>
    		<ol class="breadcrumb">
                <li></li>
                <li></li>
            </ol>
    	</div>
    </section>
    <!-- Banner Area -->

    <!-- Easy cash Area -->
    <section class="easy_cash_area cot-credict">
        <div class="container">
            <div class="row">
                <div class="col-lg-7 back-form">
                    <form class="from_main" action="php/contact.php" method="post" id="phpcontactform" novalidate="novalidate">
                        <div class="row">
                            <div class="col-lg-6 select_from">
                                <h6>Cunto cuesta el vehculo?*</h6>
                                <input type="text" class="post_select" id="precio" name="precio" v-only-numbers v-model="form.vehiclePrice" :class="{ 'error': $v.form.vehiclePrice.$invalid }" required>
                                <span><small>&nbsp;</small></span>
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Cunto puedes dar de enganche?*</h6>
                                <input type="text" class="post_select" id="enganche" name="enganche" v-only-numbers v-model="form.vehicleHitch" :class="{ 'error': $v.form.vehicleHitch.$invalid }" required>
                                <span><small>Se necesita mnimo el 20% de enganche</small></span>
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Modelo*</h6>
                                <select class="post_select" id="model" :class="{ 'error': $v.form.selectedModel.$invalid }">
                                  <option value="">Modelo*</option>
                                  <option v-for="model in models">{{model}}</option>
                                </select>
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Marca*</h6>
                                <select class="post_select" id="brand" :class="{ 'error': $v.form.selectedBrand.$invalid }" :disabled="form.selectedModel == ''">
                                  <option value="">Marca*</option>
                                  <option v-for="brand in brands">{{brand}}</option>
                                </select>
                            </div>
                            <div class="col-lg-12 select_from">
                                <h6>Versin*</h6>
                                <select class="post_select2" id="version" :class="{ 'error': $v.form.selectedVersion.$invalid }" :disabled="form.selectedBrand == ''">
                                  <option value="">Versin*</option>
                                  <option v-for="version in versions" >{{version}}</option>
                                </select>
                            </div>
                            <div class="col-lg-12 select_from" >
                                <h6>Descripcin*</h6>
                                <select class="post_select2" id="description" :class="{ 'error': $v.form.selectedDescription.$invalid }" :disabled="form.selectedVersion == ''">
                                    <option value="">Descripcin*</option>
                                    <option v-for="description in descriptions">{{description}}</option>
                                </select>
                            </div>
                            <div class="col-12">
                                <p>*El valor de la cuota mensual es estimado y no incluye valor del seguro.</p><br>
                            </div>
                            <!-- <div class="form-group">
                                <button class="theme_btn btn" id="js-contact-btn" type="submit">Enviar</button>
                                <div id="js-contact-result" data-success-msg="Form submitted successfully." data-error-msg="Messages Successfully"></div>
                            </div> -->
                        </div>
                    </form>
                </div>
                <div class="col-lg-5 text-center">
                    <div class="your_results">
                        <h3>Cuotas mensuales</h3>
                        <hr>
                        <p>Para saber las cuotas primero <br> ingresa todos los campos</p><br>
                        <div>
                            <h4 class="ra text-your_results">Cuota mensual</h4>
                            <h4><span>${{(form.vehiclePrice == 0 ? '--' : formatPrice(
                              ((form.vehiclePrice - form.vehicleHitch) / form.monthsNumber) + (((form.vehiclePrice - form.vehicleHitch) / 100) * 2.32) + 199 + 40
                            ))}}</span></h4>
                        </div>
                        <div class="form">
                            <form>
                                <fieldset class="form__options">
                                    <p>Selecciona el nmero de meses*</p>
                                    <p class="form__answer">
                                        <input type="radio" name="match" id="match_1" value="12" v-model="form.monthsNumber">
                                        <label for="match_1">
                                            12
                                        </label>
                                    </p>
                                    <p class="form__answer">
                                        <input type="radio" name="match" id="match_2" value="24" v-model="form.monthsNumber">
                                        <label for="match_2">
                                            24
                                        </label>
                                    </p>
                                    <p class="form__answer">
                                        <input type="radio" name="match" id="match_3" value="36" v-model="form.monthsNumber">
                                        <label for="match_3">
                                            36
                                        </label>
                                    </p>
                                    <p class="form__answer">
                                        <input type="radio" name="match" id="match_4" value="48" v-model="form.monthsNumber" checked>
                                        <label for="match_4">
                                            48
                                        </label>
                                    </p>
                                </fieldset>
                            </form>
                        </div>
                        <div class="col-12 white-hr"><hr>
                        </div>
                        <div>
                            <h4 class="ra text-your_results">Monto a financiar</h4>
                            <h4 class="text-your_results_2">Precio del vehculo - enganche</h4>
                            <h4><span>${{(form.vehiclePrice == 0 ? '--' : formatPrice(form.vehiclePrice - form.vehicleHitch))}}</span></h4>
                        </div>
                        <div>
                            <h4 class="ra text-your_results">Enganche</h4>
                            <!-- <h4 class="ra text-your_results">Pagos de contado:</h4>
                            <h4 class="ra text-your_results">Comisin de apertura 2.5%</h4>
                            <h4 class="text-your_results_2">2.5% del valor del monto a financiar</h4> -->
                            <h4><span>${{(form.vehiclePrice == 0 ? '--' : formatPrice(form.vehicleHitch))}}</span></h4><br>
                            <!-- <h4><span>${{(form.vehiclePrice == 0 ? '--' : formatPrice(((form.vehiclePrice - form.vehicleHitch) / 100) * 2.5))}}</span></h4><br> -->
                        </div>
                        <div class="col-12 ">
                            <button class="theme_btn btn btn-block" style="width: 100%" :disabled="$v.form.$invalid" v-on:click="createCreditQuotation()">Aplicar ahora</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    <!-- End Easy cash Area -->

    <!-- Map and From Area -->
    <section class="inicio-contacto-2 map_from_area">
        <div class="map_area">
            <div class="map_content">
                <iframe src="https://www.google.com/maps/d/u/0/embed?mid=15t3qJqAwN9WTPYplZFk1lDq5Y4N_FqVK"
                    height="671" frameborder="0" style="border:0; width: 100%" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
            </div>
        </div>
        <div class="contact_inner contact-form-title">
            <form class="from_main" novalidate="novalidate">
                <h2>Tienes alguna pregunta que hacer? <br><span>por favor, escrbenos</span> </h2>
                <div class="form-group">
                    <input type="text" class="form-control" id="name" name="name" placeholder="Nombre" v-model="contactForm.name" :class="{ 'error': $v.contactForm.name.$invalid }">
                </div>
                <div class="form-group">
                    <input type="email" class="form-control" id="email" name="email" placeholder="Correo electrnico" v-model="contactForm.email" :class="{ 'error': $v.contactForm.email.$invalid }">
                </div>
                <div class="form-group">
                    <input type="text" class="form-control" id="subject" name="subject" placeholder="Asunto" v-model="contactForm.subject" :class="{ 'error': $v.contactForm.subject.$invalid }">
                </div>
                <div class="form-group">
                    <textarea class="form-control" id="message" name="message" placeholder="Mensaje" v-model="contactForm.text" :class="{ 'error': $v.contactForm.text.$invalid }"></textarea>
                </div>
                <div class="form-group">
                    <button class="theme_btn btn" id="js-contact-btn" type="button" v-on:click="sendContact()" :disabled="$v.contactForm.$invalid">Enviar</button>
                    <div id="js-contact-result" data-success-msg="Form submitted successfully." data-error-msg="Messages Successfully"></div>
                </div>
            </form>
        </div>
    </section>
    <!-- End Map and From Area -->
  </div>
</template>

<script>
  import { required, minLength, minValue, maxValue } from 'vuelidate/lib/validators';

  export default {
    name: 'QuoteCredit',
    data() {
      return {
        models: [],
        brands: [],
        versions: [],
        descriptions: [],
        form: {
          selectedModel: '',
          selectedBrand: '',
          selectedVersion: '',
          selectedDescription: '',
          vehiclePrice: 0,
          vehicleHitch: 0,
          monthsNumber: 48
        },
        contactForm: {
          name: '',
          email: '',
          subject: '',
          text: '',
        }
      }
    },
    validations() {
      return {
        form: {
          selectedModel: { required },
          selectedBrand: { required },
          selectedVersion: { required },
          selectedDescription: { required },
          vehiclePrice: { required, minValue: minValue(1) },
          vehicleHitch: { required, minValue: minValue((this.form.vehiclePrice/100)*20), maxValue: maxValue(this.form.vehiclePrice) }
        },
        contactForm: {
          name: { required },
          email: { required },
          subject: { required },
          text: { required }
        }
      }
    },
    methods: {
      sendContact() {
        var data = {
          data: {
            name: this.contactForm.name,
            email: this.contactForm.email,
            subject: this.contactForm.subject,
            message: this.contactForm.text,
          }
        };
        this.$InsuranceService.sendContact(data).then((result) => {
          alert('Exito, su peticin ha sido enviada con exito');
          this.contactForm.name = '';
          this.contactForm.email = '';
          this.contactForm.subject = '';
          this.contactForm.text = '';
          window.scrollTo(0, 0);
        }, (err) => {
          alert(err.msg);
        });
      },
      status(validation) {
    	  return validation.$invalid;
      },
      refreshSelects() {
        $('#model').niceSelect('update');
        $('#brand').niceSelect('update');
        $('#version').niceSelect('update');
        $('#description').niceSelect('update');
      },
      getModels() {
        this.$InsuranceService.getModels().then((result) => {
          this.models = result.data.model.slice(0,14);
          setTimeout(() => {
            this.refreshSelects();

            $('#model').on('change', () => {
              this.form.selectedModel = $('#model').val();
              $('#brand').val('');
              $('#version').val('');
              $('#description').val('');
              this.form.selectedBrand = '';
              this.form.selectedVersion = '';
              this.form.selectedDescription = '';

              this.getBrands();

              setTimeout(() => {
                this.refreshSelects();
              }, 100);
            });
          }, 100);
        }, (err) => alert(err.msg));
      },
      getBrands() {
        if (this.form.selectedModel != '') {
          const data = {
            model: this.form.selectedModel
          };

          this.$InsuranceService.getBrands(data).then((result) => {
            this.brands = result.data.brand;

            setTimeout(() => {
              this.refreshSelects();

              $('#brand').on('change', () => {
                this.form.selectedBrand = $('#brand').val();
                $('#version').val('');
                $('#description').val('');
                this.form.selectedVersion = '';
                this.form.selectedDescription = '';

                this.getVersions();

                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err.msg));
        }
      },
      getVersions() {
        if (this.form.selectedBrand != '') {
          const data = {
            model: this.form.selectedModel,
            brand: this.form.selectedBrand
          };

          this.$InsuranceService.getVersions(data).then((result) => {
            this.versions = result.data.version;

            setTimeout(() => {
              this.refreshSelects();

              $('#version').on('change', () => {
                this.form.selectedVersion = $('#version').val();
                $('#description').val('');
                this.form.selectedDescription = '';

                this.getDescriptions();
                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err.msg));
        }
      },
      getDescriptions() {
        if (this.form.selectedVersion != '') {
          const data = {
            model: this.form.selectedModel,
            brand: this.form.selectedBrand,
            version: this.form.selectedVersion
          };

          this.$InsuranceService.getDescriptions(data).then((result) => {
            this.descriptions = result.data.description;

            setTimeout(() => {
              this.refreshSelects();

              $('#description').on('change', () => {
                this.form.selectedDescription = $('#description').val();

                setTimeout(() => {
                  this.refreshSelects();
                }, 100);
              });
            }, 100);
          }, (err) => alert(err.msg));
        }
      },
      createCreditQuotation() {
          const data = {
            vehicle_price: Number(this.form.vehiclePrice),
            vehicle_hitch: Number(this.form.vehicleHitch),
            months: Number(this.form.monthsNumber),
            vehicle_model: this.form.selectedModel,
            vehicle_brand: this.form.selectedBrand,
            vehicle_version: this.form.selectedVersion,
            vehicle_description: this.form.selectedDescription,
          };

          this.$UserService.createCreditQuotation({data: data}).then((result) => {
            alert('Se han mandado sus datos con xito!');
            window.location = '/pre-approval';
          }, (err) => alert(err.msg));
      },
      formatPrice(value) {
          let num = (value/1).toFixed(2);
          return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
    },
    created() {
      this.getModels();
    },
  }
</script>

<style lang="scss">
  .error {
    border-color: red !important;
    background: #FDD;
  }

  .error:focus {
    outline-color: #F99;
  }

  .nice-select .current {
      width: -webkit-fill-available !important;
      display: block;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
  }
</style>



