<script>
import { required, minLength, between, maxValue, minValue, email } from 'vuelidate/lib/validators';
import statesAndTownship from '../assets/estados-municipios.json';

var regexEmail = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/;
var regexPhone = /^([0-9])*$/;
var regexRfc = /^([A-Z&]{3,4}) ?(?:- ?)?(\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])) ?(?:- ?)?([A-Z\d]{2})([A\d])$/;

export default {
  name: 'QuotationProcess',
  props: {
    dataContractor: {
      id: 0,
      fullName: '',
      age: '',
      birthDay: '',
      email: '',
      phone: '',
      gender: '',
      postcode: '',
      paymentMethod: '',
      selectedModel: '',
      selectedBrand: '',
      selectedVersion: '',
      selectedDescription: ''
    },
    dataInsurance: {
      id: 0,
      cia: '',
      duty: '',
      initial_payment: 0,
      iva: '',
      material_damage: 0,
      net_price: '',
      payments: '',
      price_total: '',
      recharges: '',
      steal_total: 0
    },
    vehicleKey: '',
  },
  data() {
    return {
      arrayValidations: [],
      states: [],
      townships: [],
      step1: true,
      step2: false,
      step3: false,
      stepCard: false,
      stepFinal: false,
      isLoading: false,
      models: [],
      brands: [],
      versions: [],
      descriptions: [],
      pay_method: '',
      token: '',
      endpoint_sandbox_openpay: 'https://sandbox-api.openpay.mx/v1/',
      endpoint_payament: 'you_host/api/charge',
      deviceSessionId: '',
      openpay_id: 'mepd5ufg3yus1qycakcs',
      openpay_key: 'pk_9f6ec98a6c024c349d05fa6f39852cc1',
      openpay_sandbox_mode: true,
      policieId: 0,
      pricePolicy: 0,
      policy: '',
      data: {
        fullName: '',
        age: '',
        email: '',
        phone: '',
        gender: '',
        postcode: '',
        paymentMethod: '',
        selectedModel: '',
        selectedBrand: '',
        selectedVersion: '',
        selectedDescription: '',
        id: 0,
        cia: '',
        material_damage: '',
        steal_total: '',
        initial_payment: 0
      },
      detailsData: {
        names: '',
        father_name: '',
        mother_name: '',
        birth_date: '',
        marital_status: 'Soltero',
        rfc: '',
        entity: 'Fisica',
        business_name: '',
        constancy_date: '',
        street: '',
        exterior_no: '',
        interior_no: '',
        zip_code: '',
        suburb: '',
        state: '',
        city: '',
        beneficiary_name: '',
        beneficiary_birth_date: '',
        beneficiary_rfc: '',
        serial_number: '',
        plates: '',
        vehicle_type: 'Auto_Particulares',
        invoice_date: '',
        transmission: 'Manual',
        passengers: '',
        start_date: '',
        end_date: '',
      },
      cardData: {
        type: 'Dbito',
        name: '',
        number: '',
        expiry: '',
        cvv: '',
      },
    }
  },
  validations() {
    return {
      detailsData: {
        names: { required },
        father_name: { required },
        mother_name: { required },
        marital_status: { required },
        rfc: { required, validateRfc: (value) => { return regexRfc.test(value); } },
        entity: { required },
        street: { required },
        exterior_no: { required },
        zip_code: { required },
        suburb: { required },
        state: { required },
        city: { required },
        beneficiary_rfc: { validateRfc: (value) => { return regexRfc.test(value); } },
        serial_number: { required },
        plates: { required },
        vehicle_type: { required },
        passengers: { required, minLength: minLength(1) },
        transmission: { required },
      },
      cardData: {
        type: { required },
        name: { required },
        number: { required, minLength: minLength(19)},
        expiry: { required, minLength: minLength(9) },
        cvv: { required },
      }
    }
    
  },
  methods: {
    validateForm() {
      var errors = 0;

      if (this.detailsData.marital_status == '') { errors++; this.arrayValidations.push('Estado civil'); } 
      // if (this.detailsData.birth_date == '') { errors++; this.arrayValidations.push('Fecha de nacimiento'); } 
      if (this.detailsData.rfc == '') { errors++; this.arrayValidations.push('RFC'); } 
      if (this.detailsData.entity == '') { errors++; this.arrayValidations.push('Tipo de entidad'); } 
      if (this.detailsData.street == '') { errors++; this.arrayValidations.push('Calle'); } 
      if (this.detailsData.exterior_no == '') { errors++; this.arrayValidations.push('Nmero exterior'); } 
      if (this.detailsData.zip_code == '') { errors++; this.arrayValidations.push('Cdigo postal'); } 
      if (this.detailsData.suburb == '') { errors++; this.arrayValidations.push('Colonia'); } 
      if (this.detailsData.state == '') { errors++; this.arrayValidations.push('Estado'); } 
      if (this.detailsData.city == '') { errors++; this.arrayValidations.push('Ciudad'); } 
      if (this.detailsData.serial_number == '') { errors++; this.arrayValidations.push('Nmero serial'); } 
      if (this.detailsData.plates == '') { errors++; this.arrayValidations.push('Placas'); } 
      if (this.detailsData.vehicle_type == '') { errors++; this.arrayValidations.push('Tipo de vehculo'); } 
      if (this.detailsData.transmission == '') { errors++; this.arrayValidations.push('Transmisin'); } 
      if (this.detailsData.passengers == '') { errors++; this.arrayValidations.push('Pasajeros'); } 
      return (errors > 0);
    }, 
    selectDate() {
      $('#birth_date2').datepicker('show');
    },
    selectDate2() {
      $('#constancy_date').datepicker('show');
    },
    selectDate3() {
      $('#beneficiary_birth_date').datepicker('show');
    },
    selectDate4() {
      $('#invoice_date').datepicker('show');
    },
    selectDate5() {
      $('#start_date').datepicker('show');
    },
    selectDate6() {
      $('#end_date').datepicker('show');
    },
    getStates() {
      this.states = Object.keys(statesAndTownship);
      setTimeout(() => {
          setTimeout(() => {
              $('#state').on('change', () => {
                this.detailsData.state = $('#state').val();
                $('#city').val('');
                this.detailsData.city = '';
                this.getTownships();
              });
          }, 1000);
      }, 1000);
    },
    getTownships() {
      this.townships = statesAndTownship[this.detailsData.state];
      setTimeout(() => {
        $('#state').niceSelect('update');
        $('#city').niceSelect('update'); 
          setTimeout(() => {
              $('#city').on('change', () => {
                this.detailsData.city = $('#city').val();
                setTimeout(() => {
                  $('#state').niceSelect('update');
                  $('#city').niceSelect('update'); 
                }, 100);
              });
          }, 100);
      }, 100);
    },
    nextStep2() {
      if ($('#constancy_date').val()) {
        this.detailsData.constancy_date = moment($('#constancy_date').val(), 'DD-MM-YYYY').format('YYYY-MM-DD');
      } 
      // if ($('#birth_date2').val()) {
        //   this.detailsData.birth_date = moment($('#birth_date2').val(), 'DD-MM-YYYY').format('YYYY-MM-DD');
      // } else {
        //   alert('Favor de llenar la fecha de nacimiento');
      //   return
      // }
      if ($('#invoice_date').val()) {
        this.detailsData.invoice_date = moment($('#invoice_date').val(), 'DD-MM-YYYY').format('YYYY-MM-DD');
      }
      this.isLoading = true;
      this.detailsData.marital_status = $('#estado_civil').val();
      this.detailsData.entity = $('#entity').val();
      this.detailsData.vehicle_type = $('#vehicle_type').val();
      this.detailsData.transmission = $('#transmission').val();
      if ($('#beneficiary_birth_date').val()) {
        this.detailsData.beneficiary_birth_date = moment($('#beneficiary_birth_date').val(), 'DD-MM-YYYY').format('YYYY-MM-DD');
      }

      var day = new Date().getDate();
      var month = new Date().getMonth() + 1;
      var year = new Date().getFullYear(); 

      var dataPdf = {
        id_quote: this.dataInsurance.id,
        mother_name: this.detailsData.mother_name,
        last_name: this.detailsData.father_name,
        phone: this.dataContractor.phone,
        email: this.dataContractor.email,
        age: this.dataContractor.age,
        civil_status: this.detailsData.marital_status,
        const_date: this.detailsData.constancy_date,
        birthday: this.dataContractor.birthDay,
        name: this.detailsData.names,
        rfc: this.detailsData.rfc,
        gender: this.dataContractor.gender,
        entity: this.detailsData.entity,
        zip_code: this.detailsData.zip_code,
        street: this.detailsData.street,
        suburb: this.detailsData.suburb,
        state: this.detailsData.state,
        municipality: this.detailsData.city,
        exterior: this.detailsData.exterior_no,
        interior: this.detailsData.interior_no,
        discount: '-1',
        payment: this.dataContractor.paymentMethod,
        // invoice_date: this.detailsData.invoice_date || (year + '-' + month + '-' + this.returnDay(day)),
        invoice_date: this.detailsData.invoice_date,
        end_date: (year + 1) + '-' + month + '-' + this.returnDay(day),
        start_date: year + '-' + month + '-' + this.returnDay(day),
        motor: 'SN',
        model: this.dataContractor.selectedModel,
        license_plate: this.detailsData.plates,
        hedge: 'Amplia',
        serie: this.detailsData.serial_number,
        vehicle_type: this.detailsData.vehicle_type,
        invoice_value: '0',
        origin: 'Web',
        brand: this.dataContractor.selectedBrand,
        year: this.dataContractor.selectedModel,
        transmission: this.detailsData.transmission,
        passengers: this.detailsData.passengers,
        description: this.dataContractor.selectedDescription,
        version: this.dataContractor.selectedVersion,
        cia: this.dataInsurance.cia,
        clave_auto: this.vehicleKey
      }
      if (!this.validateForm()) {
        this.$PolicyService.generateInsurance(dataPdf).then((result) => {
          this.policieId = result.data.id;
          this.policy = result.data.poliza;
          this.pricePolicy = result.data.invoiceValue;
          this.isLoading = false;
          this.step1 = false;
          this.step2 = true;
          window.scrollTo(0, 0);
        }, (err) => {
          alert(err.message);
          this.isLoading = false;
          window.scrollTo(0, 0);
        });
        
      } else {
        let campos = '';
        this.arrayValidations.forEach(item => {
          campos += item + ' ';
        })
        alert(`Favor de completar los campos obligatorios: ${campos}`);
        this.isLoading = false;
      }
    },
    returnDay(day) {
      if (day.toString().length == 1) {
        return '0' + day; 
      } 

      return day;
    },
    nextStep3() {
      this.step2 = false;
      this.step3 = true;
      window.scrollTo(0, 0);
    },
    nextStep4() {
      this.isLoading = true;
      var dataUser = {
        name: this.detailsData.names + ' ' + this.detailsData.father_name + ' ' + this.detailsData.mother_name,
        email: this.dataContractor.email,
        phone_number: this.dataContractor.phone
      };
      if (this.pay_method == 'bank_account') {

        var customerId = '';
        var data = {
          method: 'bank_account',
          customerId: '',
          currency: 'MXN'
        };

        this.$PolicyService.createUser(dataUser).then((res) => {
          customerId = res.data.id;
          data.customerId = customerId;
          this.$PolicyService.payInsurance(data, this.policieId).then((result) => {
            window.open(`https://sandbox-dashboard.openpay.mx/spei-pdf/${this.openpay_id}/${result.data.id}`, '_blank');
            this.isLoading = false;
            alert('xito, se ha generado su pago con xito');
            window.location = '/';
          }, (err) => {
            this.isLoading = false;
            alert(err.message);
          });
        }, (err) => {
          this.isLoading = false;
          alert(err.message);
        });

        
      } else if (this.pay_method == 'card') {
        this.isLoading = false;
        this.step3 = false;
        this.stepCard = true;
      } else {

        var data = {
          method: 'store',
          customerId: '',
          currency: 'MXN'
        }

        this.$PolicyService.createUser(dataUser).then((res) => {
          customerId = res.data.id;
          data.customerId = customerId;
          this.$PolicyService.payInsurance(data, this.policieId).then((result) => {
            window.open(`https://sandbox-dashboard.openpay.mx/paynet-pdf/${this.openpay_id}/${result.data.payment_method.reference}`, '_blank');
            this.isLoading = false;
            alert('xito, se ha generado su pago con xito');
            window.location = '/';
          }, (err) => {
            this.isLoading = false;
            alert(err.message);
          });
        }, (err) => {
          this.isLoading = false;
          alert(err.message);
        });

        
      }
      
    },
    generatePay() {
      this.cardData.type = $('#card_type').val();
      var arrayCard = this.cardData.number.split(' ');
      var arrayExpiry = this.cardData.expiry.split(' ');
      
      var data = {
        card_number: arrayCard[0] + arrayCard[1] + arrayCard[2] + arrayCard[3],
        holder_name: this.cardData.name,
        expiration_year: arrayExpiry[2].slice(2,4),
        expiration_month: arrayExpiry[0],
        cvv2: this.cardData.cvv,
      };
      if (data.card_number) {
        this.isLoading = true;
        this.pay(data);
      }
    },
    initOpenPay() {
      OpenPay.setId(this.openpay_id);
      OpenPay.setApiKey(this.openpay_key);
      OpenPay.setSandboxMode(this.openpay_sandbox_mode);
      this.deviceSessionId = OpenPay.deviceData.setup();
    },
    pay(data) {
      OpenPay.token.create({
        card_number: data.card_number,
        holder_name: data.holder_name,
        expiration_year: data.expiration_year,
        expiration_month: data.expiration_month,
        cvv2: data.cvv2
        
      }, (response) => {
        this.token = response.data.id;
        var data = {
          sourceId : this.token,
          method : "card",
          deviceSessionId : this.deviceSessionId,
          customer : {
            name : this.detailsData.names,
            last_name : this.detailsData.father_name + ' ' + this.detailsData.mother_name,
            phone_number : this.dataContractor.phone,
            email : this.dataContractor.email
          }
        }

        this.$PolicyService.payInsurance(data, this.policieId).then((res) => {
          this.stepCard = false;
          this.stepFinal = true;
          this.isLoading = false;
        }, (err) => {
          alert(err.message);
          this.isLoading = false;
        });
      }, (error) => {
        console.log(error);
        this.isLoading = false;
      });

      
    },
    formatPrice(value) {
      let num = (value/1).toFixed(2);
      return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    assignData() {
      this.data.fullName = this.dataContractor.fullName;
      this.data.age = this.dataContractor.age;
      this.data.email = this.dataContractor.email;
      this.data.phone = this.dataContractor.phone;
      this.data.gender = this.dataContractor.gender;
      this.data.postcode = this.dataContractor.postcode;
      this.data.paymentMethod = this.dataContractor.paymentMethod;
      this.data.selectedModel = this.dataContractor.selectedModel;
      this.data.selectedBrand = this.dataContractor.selectedBrand;
      this.data.selectedVersion = this.dataContractor.selectedVersion;
      this.data.selectedDescription = this.dataContractor.selectedDescription;
      this.data.id = this.dataInsurance.id;
      this.data.cia = this.dataInsurance.cia;
      this.data.material_damage = this.dataInsurance.material_damage;
      this.data.steal_total = this.dataInsurance.steal_total;
      this.data.initial_payment = this.dataInsurance.initial_payment;
    },
  },
  created() {
    this.assignData();
    this.getStates();
  },
  mounted() {
    this.initOpenPay();
    
    var card = new Card({
      form: '#formId',
      container: '.card-wrapper',

      placeholders: {
        number: '**** **** **** ****',
        name: 'Nombre completo',
        expiry: '**/****',
        cvc: '***'
      }
    });
  }
}
</script>

