  .custom-radio .custom-control-input:checked~.custom-control-label::after {
    background-color: #f59019 !important; 
    /* this bg image SVG is just a white circle, you can replace it with any valid SVG code */
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3E%3Ccircle r='3' fill='%23fff'/%3E%3C/svg%3E") !important; 
    border-radius: 100% !important;
    width: 20px;
    height: 20px;
  }

  /* active state i.e. displayed while the mouse is being pressed down */
  .custom-radio .custom-control-input:active ~ .custom-control-label::before {
    color: #fff !important;
    background-color: #2759ab !important;
  }

  /* the shadow; displayed while the element is in focus */
  .custom-radio .custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #fff, 0 0 0 0.2rem rgba(255, 123, 255, 0.25) !important; /* pink, 25% opacity */
  }

  input[type="radio"]:checked~label {
    opacity: 1 !important;
    border: 0px solid transparent !important;
    background-color: transparent !important;
  }

</style>


