                    </div>
                    <div class="col-lg-6 select_from">
                        <h6>Telfono 1*</h6>
                        <input type="tel" class="post_select" id="phone1" name="phone1" v-only-numbers v v-model="form.tel1" :class="{ 'error': $v.form.tel1.$invalid }">
