                          <option value="">Estado</option>
                          <option v-for="state in states">{{state}}</option>
                        </select>
                    </div>
                    <div class="col-lg-6 select_from">
                        <h6>Municipio*</h6>
                        <select class="post_select" id="township" :class="{ 'error': $v.form.city.$invalid }" :disabled="form.state == ''">
