                    <!-- <div class="col-lg-6 select_from">
                        <h6>Nombre de la empresa*</h6>
                        <input type="text" class="post_select" id="empresa" name="empresa" v-model="form.company" :class="{ 'error': $v.form.company.$invalid }">
