<script>
  import statesAndTownship from '../assets/estados-municipios.json';
  import { required, minLength, email } from 'vuelidate/lib/validators';

  var regexRfc = /^([A-Z&]{3,4}) ?(?:- ?)?(\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])) ?(?:- ?)?([A-Z\d]{2})([A\d])$/;
  export default {
    name: 'BusinessPartners',
    data() {
      return {
        isLoading: false,
        states: [],
        townships: [],
        form: {
          username: '',
          fullname: '',
          email: '',
          password: '',
          loteName: '',
          address: '',
          tel1: '',
          tel2: '',
          tel3: '',
          rfc: '',
          state: '',
          city: '',
          zipCode: ''
        }
      }
    },
    validations() {
      return {
        form: {
          username: { required },
          fullname: { required },
          email: { required, email },
          password: { required, minLength: minLength(12) },
          loteName: { required },
          address: { required },
          tel1: { required },
          rfc: { required, validateRfc: (value) => { return regexRfc.test(value); } },
          state: { required },
          city: { required },
          zipCode: { required }
        }
      }
    },
    methods: {
      refreshSelects() {
        setTimeout(() => {
          $('#state').niceSelect('update');
          $('#township').niceSelect('update');
        }, 100);
      },
      getStates() {
        this.states = Object.keys(statesAndTownship);

        setTimeout(() => {
            this.refreshSelects();

            setTimeout(() => {
                $('#state').on('change', () => {
                  this.form.state = $('#state').val();
                  $('#township').val('');
                  this.form.city = '';

                  this.getTownships();
                });
            }, 100);
        }, 100);
      },
      getTownships() {
        this.townships = statesAndTownship[this.form.state];

        setTimeout(() => {
            this.refreshSelects();

            setTimeout(() => {
                $('#township').on('change', () => {
                  this.form.city = $('#township').val();
                  setTimeout(() => {
                      this.refreshSelects();
                  }, 100);
                });
            }, 100);
        }, 100);
      },
      createBusinessPartner() {
        this.isLoading = true;

        var data = {
          name: this.form.fullname,
          last_name: '',
          email: this.form.email,
          phone: this.form.tel1,
          phone2: this.form.tel2,
          phone3: this.form.tel3,
          state: this.form.state,
          township: this.form.city,
          company: this.form.loteName,
          address: this.form.address,
          postcode: this.form.zipCode,
          rfc: this.form.rfc,
        };


        this.$BusinessPartnersService.createBusinessPartner(this.form).then((result) => {
          this.$UserService.createBusinessPartner({data: data}).then((result) => {
            alert('Tu solicitud fue enviada a revisin con uno de nuestros asesores. Te notificaremos por medio de correo electrnico una vez que quede aprobada. !Gracias por registrarte en Credimvil!');
            window.location = '/';
            this.isLoading = false;
          }, (err) => {
            this.isLoading = false;
            alert(err.msg)
          });
          
        }, (err) => {
          alert(err.message);
          this.isLoading = false;
        });
      },
    },
    created() {
      this.getStates();
    },
  }
</script>

<style lang="scss">
  .error {
    border-color: red;
    background: #FDD;
  }

  .error:focus {
    outline-color: #F99;
  }

  .nice-select .current {
      width: -webkit-fill-available !important;
      display: block;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
  }
</style>



