</template>

<script>
  import { required, minLength, between } from 'vuelidate/lib/validators';

  export default {
    name: 'Contact',
    data() {
      return {
        contactForm: {
          name: '',
          email: '',
          subject: '',
          text: '',
        }
      }
    },
    validations() {
      return {
        contactForm: {
          name: { required },
          email: { required },
          subject: { required },
          text: { required }
        }
      }
    },
    methods: {
      sendContact() {
        var data = {
          data: {
            name: this.contactForm.name,
            email: this.contactForm.email,
            subject: this.contactForm.subject,
            message: this.contactForm.text,
          }
        };
        this.$InsuranceService.sendContact(data).then((result) => {
          alert('Exito, su peticin ha sido enviada con exito');
          window.location = '/';
        }, (err) => {
          alert(err.msg);
        });
      },
    },
  }
</script>

<style lang="scss">

  .error {
    border-color: red !important;
  }
</style>



