<script>
  import statesAndTownship from '../assets/estados-municipios.json';
  import statesOriginal from '../assets/estados-municipiosOriginal.json';
  import statesKey from '../assets/clave-estados.json';
  import { required, minLength, between } from 'vuelidate/lib/validators';

  var regexCurp = /^$|\s+|^NULL$|^([A-Z][AEIOUX][A-Z]{2}\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])[HM](?:AS|B[CS]|C[CLMSH]|D[FG]|G[TR]|HG|JC|M[CNS]|N[ETL]|OC|PL|Q[TR]|S[PLR]|T[CSL]|VZ|YN|ZS)[B-DF-HJ-NP-TV-Z]{3}[A-Z\d])(\d)$/;
  var regexRfc = /^([A-Z&]{3,4}) ?(?:- ?)?(\d{2}(?:0[1-9]|1[0-2])(?:0[1-9]|[12]\d|3[01])) ?(?:- ?)?([A-Z\d]{2})([A\d])$/;

  export default {
    name: 'PreApproval',
    data() {
      return {
        states: [],
        statesOrigin: [],
        townships: [],
        suburbs: [],
        birthDate: '',
        clickupTaskId: '',
        detailsData: {
          data: {
            first_name: '',
            last_name: '',
            father_name: '',
            mother_name: '',
            DOB: '',
            state_dob: '',
            gender: 'Male',
            nacionality: 'MXN',
            RFC: '',
            CURP: '',
            marital_status: 'Single',
          }
        },
        employmentData: {
          data: {
            employment_situation: 'fixed',
            antique: 'less_than_one_year',
            net_income: '',
            default_ocupation: false,
          }
        },
        userData: {
          data: {
            name: '',
            email: '',
            phone: '',
            a_step: 0,            
            clave_asesor: ''
          }
        },
        addressData: {
          data: {
            country: 'MX',
            street: '',
            address_no: '',
            address_inside_no: '',
            state: '',
            town: '',
            suburb: '',
            postcode: '',
            current_living: '',
            living_since: '',
            main_address: 1
          }
        },
        confirmationData: {
          code: ''
        },
        currentStep: 1,
        defaultOcupation: 'no',
        confirmations: ["confirm_one", "confirm_two"], 
      }
    },
    validations() {
      return {
        detailsData: {
          data: {
            first_name: { required },
            father_name: { required },
            gender: { required },
            state_dob: { required },
            RFC: { required, validateRfc: (value) => { return regexRfc.test(value)} },
            CURP: { validateCurp: (value) => { return regexCurp.test(value)} },
          }
        },
        employmentData: {
          data: {
            default_ocupation: { required },
          }
        },
        addressData: {
          data: {
            street: { required },
            address_no: { required },
            state: { required },
            town: { required },
            suburb: { required },
            postcode: { required }
          }
        },
        confirmationData: {
          code: { required }
        },
      }
    },
    methods: {
      datalistValidator(modelname, modelList) {

        var result = (modelList.indexOf(modelname) > -1);
        if(!result){
          alert("Seleccione un elemento de la lista");
        }
        return result;
      },
      refreshSelects() {
          setTimeout(() => {
            $('#state').niceSelect('update');
            $('#state_dob').niceSelect('update');
            $('#township').niceSelect('update');
            $('#colonia').niceSelect('update');
            $('#postal').niceSelect('update');
            $('#birth-date').datepicker({
              dateFormat: 'dd-mm-yy',
              changeMonth: true,
              changeYear: true,
              yearRange: "-100:+0",
            });          
          }, 100);
      },
      getStates() {
        this.states = Object.keys(statesAndTownship);
        this.statesOrigin = Object.keys(statesOriginal);
        this.refreshSelects();
      },
      getTownships() {
        if(this.addressData.data.state != "" && this.userData.data.a_step == 2){
          this.townships = statesAndTownship[this.addressData.data.state];
          this.refreshSelects();
        }
      },
      getSuburb() {  
        if(this.addressData.data.town != "" && this.userData.data.a_step == 2){  
          this.$CopomexService.getColoniaByMunicipio(this.addressData.data.town)
          .then((result) => {
            this.suburbs = Object.values(result.response.colonia);              
            this.refreshSelects();              
          });
        }
      },
      getCp() {  
        if($('#postal').val() == ''){
          this.$CopomexService.getCp(this.addressData.data.state,this.addressData.data.town, this.addressData.data.suburb)
          .then((result) => {               
            this.addressData.data.postcode = result.response.cp[0];
            this.refreshSelects();
          });
        }  
      },
      onChangeCp(){
        if($('#postal').val() != ''  && $('#postal').val().length >= 5){
            this.preloader();
            this.$CopomexService.getInfoByEstado($('#postal').val()).then((result) => {      
            
            this.suburbs = Object.values(result.response.asentamiento); 
            $('#state').val(result.response.estado);                   
            this.addressData.data.state = result.response.estado;
            $('#township').val(result.response.municipio);                   
            this.addressData.data.town = result.response.municipio;
            $('#colonia').val('');                   
            this.addressData.data.suburb = '';
            this.preloaderHiden();
            this.refreshSelects();
          }).catch(function(err) {      
            $('.preloader').fadeOut();
            $('.preloader').delay(10).fadeOut('slow');
          });
        }     
      },
      onChangeState(){
        this.addressData.data.state = $('#state').val();
        if(this.datalistValidator(this.addressData.data.state, this.states)){  
            $('#postal').val('');      
            this.addressData.data.postcode = '';          
            $('#township').val('');
            this.addressData.data.town = '';
            $('#colonia').val('');
            this.addressData.data.suburb = '';
            this.getTownships();             
        }else{           
            $('#state').val('');         
            this.detailsData.data.state = "";
            this.refreshSelects();
        } 
      },
      onChangeTown(){
        this.addressData.data.town = $('#township').val();
        if( this.datalistValidator(this.addressData.data.town, this.townships)){              
            $('#postal').val('');      
            this.addressData.data.postcode = '';               
            $('#colonia').val('');                   
            this.addressData.data.suburb = '';
            this.getSuburb();                
        }else{
            $('#township').val('');                        
            this.addressData.data.town = "";
            this.refreshSelects();
        }    
      },
      onChangeSuburb(){
        this.addressData.data.suburb = $('#colonia').val();                          
          if( this.datalistValidator(this.addressData.data.suburb, this.suburbs)){                   
              this.getCp();                
          }else{
              $('#colonia').val('');                        
              this.addressData.data.suburb = "";
              this.refreshSelects();
          } 
          this.refreshSelects(); 
      },
      onChangeStateDob(){
        this.detailsData.data.state_dob = $('#state_dob').val();
        if( this.datalistValidator(this.detailsData.data.state_dob, this.statesOrigin)){

          var ap_paterno = this.detailsData.data.father_name;
          var ap_materno =  this.detailsData.data.mother_name;
          var nombre =  this.detailsData.data.first_name + " " + this.detailsData.data.last_name;
          var rfc = moment($('#birth-date').val(), 'DD-MM-YYYY').format('YYMMDD');
          this.detailsData.data.gender = $('#genero').val();
          var sexo = $('#genero').val() == "Male"? "H":"M";                    
          var estado = statesKey[this.detailsData.data.state_dob];
          setTimeout(() => {
            var result = this.$CURPyRFC.calculaRFCyCURP(ap_paterno, ap_materno, nombre, rfc, sexo, estado);                
            this.detailsData.data.CURP = result.CURP;
            this.detailsData.data.RFC = result.RFC;
            this.refreshSelects();
          },100);
        }else{
            $('#state_dob').val('');                        
            this.detailsData.data.state_dob = "";
            this.refreshSelects();
        }     
      },
      saveStepOne() {
        this.preloader();
        this.userData.data.name = `${this.detailsData.data.first_name} ${this.detailsData.data.father_name}`;
        this.userData.data.a_step = 2;
        this.employmentData.data.default_ocupation = (this.defaultOcupation == 'yes' ? true : false);
        this.detailsData.data.DOB = moment($('#birth-date').val(), 'DD-MM-YYYY').format('YYYY-MM-DD');
        this.detailsData.data.gender = $('#genero').val();
        this.detailsData.data.nacionality = $('#nacionalidad').val();
        this.detailsData.data.marital_status = $('#estado-civil').val();
        this.$UserService.updateDetails(this.detailsData).then((result) => {
          this.$UserService.updateEmployement(this.employmentData).then((result) => {
            this.$UserService.updateUser(this.userData).then((result) => {
                this.currentStep = 2;
                this.getStates();
                this.preloaderHiden();
            }, (err) => {alert('Se requiere completar los campos requeridos de usuario');this.preloaderHiden();});
          }, (err) => {alert('Se requiere completar los campos requeridos de empleo');this.preloaderHiden();});
        }, (err) => {alert('Se requiere completar los campos requeridos de detalle');this.preloaderHiden();});
      },
      saveStepTwo() {        
        this.preloader();
        this.userData.data.a_step = 3;
        this.$UserService.updateAddress(this.addressData).then((result) => {
          this.$UserService.updateUser(this.userData).then((result) => {
            this.currentStep = 3;
            this.preloaderHiden();
          }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      saveStepThree() {        
        this.preloader();
        this.userData.data.a_step = 4;
        const confirType = {
            type: parseInt($('#confirmation-type').val())
        };

        this.$UserService.sendConfirmationCode(confirType).then((result) => {
          this.$UserService.updateUser(this.userData).then((result) => {
            this.currentStep = 4;
            this.preloaderHiden();
          }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      saveStepFour() {        
        this.preloader();
        this.userData.data.a_step = 5;

        if (this.confirmations.length == 2) {
          this.$UserService.bureauConsult().then((result) => {		
            if (result.status == 1 && result.data.approved) {
              this.$UserService.updateUser(this.userData)
              .then((result) => {                                                       
				this.$UserService.verifyConfirmationCode(this.confirmationData).then((result) => {
				  this.currentStep = 5;    
				  this.preloaderHiden();  
				}, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});                                                            
			  }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();})
              .finally(function() {                 
                  localStorage.removeItem('quote_credit');
                  //window.location = '/';
              });
            } else {
              this.userData.data.a_step = 4;
              this.$UserService.verifyConfirmationCode(this.confirmationData).then((result) => {
                this.$UserService.updateUser(this.userData)
                .then((result) => {                  
                  this.currentStep = 4;
                  this.preloaderHiden();
                }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
              }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();}) 
              .finally(function() {
                  this.preloaderHiden();
                  this.$swal('CUIDADO!','Favor de intentar de nuevo en 6 meses','warning');
                  //window.location = '/';
              });
            }
          }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});          
        } else {
         this.preloaderHiden();
         this.$swal('CUIDADO!'
                    ,'Por favor confirme que su informacin es verdica y autoriza a Credimvil para la obtencin de su historial crediticio y que est de acuerdo con que Credimvil lleve a cabo investigaciones sobre mi historial crediticio'
                    ,'warning');
        }
      },
      getUser() {
        this.$UserService.getUser().then((result) => {
          if (result.data.length > 0) {
            this.clickupTaskId = result.data[0].clickup_task;
            this.userData.data.name = result.data[0].name;
            this.userData.data.email = result.data[0].email;
            this.userData.data.phone = String(result.data[0].phone);
            this.userData.data.phone_code = result.data[0].phone_code;
            this.userData.data.a_step = result.data[0].a_step;
            this.userData.data.clave_asesor = result.data[0].clave_asesor;
            this.currentStep = this.userData.data.a_step;
            this.getStates();
          }
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      getDetails() {
        this.$UserService.getDetails().then((result) => {
          if (result.data.length > 0) {
            this.detailsData.data.first_name = result.data[0].first_name;
            this.detailsData.data.last_name = result.data[0].last_name;
            this.detailsData.data.father_name = result.data[0].father_name;
            this.detailsData.data.mother_name = result.data[0].mother_name;
            this.detailsData.data.DOB = moment(result.data[0].DOB, 'YYYY-MM-DD').format('DD-MM-YYYY');
            this.detailsData.data.gender = result.data[0].gender;
            this.detailsData.data.RFC = result.data[0].RFC;
            this.detailsData.data.CURP = result.data[0].CURP;
            this.detailsData.data.state_dob = result.data[0].state_dob;
            setTimeout(() => {
              $('#genero').val(this.detailsData.data.gender);              
              $('#birth-date').val(this.detailsData.data.DOB);
              $('#genero').niceSelect('update');        
              $('#birth-date').niceSelect('update');    
            }, 300);
          }
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      getEmployement() {
        this.$UserService.getEmployement().then((result) => {
          if (result.data.length > 0) {
            this.employmentData.data.default_ocupation = result.data[0].default_ocupation;
            this.defaultOcupation = (this.employmentData.data.default_ocupation ? 'yes' : 'no');

            setTimeout(() => {;
              $('#employment-situation').val(this.employmentData.data.employment_situation);
              $('#employment-situation').niceSelect('update');              
            }, 300);
          }
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      getAddress() {
        this.$UserService.getAddress().then((result) => {
          if (result.data.length > 0) {
            this.addressData.data.street = result.data[0].street;
            this.addressData.data.address_no = result.data[0].address_no;
            this.addressData.data.address_inside_no = result.data[0].address_inside_no;
            this.addressData.data.state = result.data[0].state;
            this.addressData.data.town = result.data[0].town;
            this.addressData.data.suburb = result.data[0].suburb;
            this.addressData.data.postcode = result.data[0].postcode;
            this.addressData.data.living_since = result.data[0].living_since;

            if (this.addressData.data.state) {
              this.getTownships();
              setTimeout(() => {
                this.getSuburb();
              }, 300);
            }
          }
        }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
      },
      selectDate() {
        $('#birth-date').datepicker('show');
      },
      continueRequest(type) {
        this.$UserService.getDetails().then((result) => {
          if (result.data.length > 0) {
            var data = {
              data: {
                clickupTaskId: this.clickupTaskId,
                bc_score: result.data[0].bc_score,
                request_type: type 
              }
            };
            if (data.data.bc_score) {
              this.$UserService.getUser().then((resultUser) => {
                if (result.data.length > 0) {
                  data.data.clickupTaskId = resultUser.data[0].clickup_task;
                    this.$UserService.requestType(data).then((res) => {
                      alert('En breve ser contactado por un asesor');
                      // window.location = '/';
                    }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
                }
                
              }, (err) => {this.$swal("CUIDADO!",err.msg,'error');this.preloaderHiden();});
            }
          }
        });        
      },
      preloader(){        
        $('.preloader').fadeIn();
        $('.preloader').delay(10).fadeIn('slow');
      },
      preloaderHiden(){        
        $('.preloader').fadeOut();
        $('.preloader').delay(10).fadeOut('slow');
      }
    },
    created() {
      this.getUser();
      this.getDetails();
      this.getEmployement();
      this.getAddress();
      this.getStates();
    },
    
  }

</script>

<style lang="scss">
  .error {
    border-color: red;
  }

  .nice-select .current {
      width: -webkit-fill-available !important;
      display: block;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
  }
</style>



