let onlyNumbers = (el) => {
    let keypress = ($event) => {
        let keyCode = ($event.keyCode ? $event.keyCode : $event.which);
        if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) { // 46 is dot
            $event.preventDefault();
        }
    }
    el.addEventListener('keypress', keypress);
}

export default onlyNumbers;



