        //     this.$InsuranceService.getVersions(data).then((result) => {
        //       result.data.version.forEach((resVersion) => {
        //         if (this.versions.indexOf(resVersion) == -1) {
        //           this.versions.push(resVersion);
        //         }
        //       });
        //       setTimeout(() => {
        //         this.refreshSelects();

        //         $('input[name=versions]:checkbox').on('change', (event) => {
        //           console.log(this.form.selectedVersion);

        //           setTimeout(() => {
        //             this.refreshSelects();
        //           }, 100);
        //         });
        //       }, 100);
        //     }, (err) => console.log(err));
        //   })
        // });
      },
      redirect(item) {
        localStorage.setItem('vehicleId', item.vehicle.id);
        window.location = '/view-car';
      },
      filterSearch() {
        this.filters = {
          "search": this.form.filter
        }
        this.currentPage = 1;
        this.filters.page = 1;
        this.getData(this.filters);
      },
      doFilter() {
        this.form.vehiclePriceMin = priceMin;
        this.form.vehiclePriceMax = priceMax;
        this.form.vehicleKmMin = kmMin;
        this.form.vehicleKmMax = kmMax;
        
        this.filters = {
          "page": 1,
          "brand": this.form.selectedBrand,
          "model": this.form.selectedVersion,
          "mileage_min": Number(this.form.vehicleKmMin),
          "mileage_max": Number(this.form.vehicleKmMax),
          "fuel": this.form.fuel,
          "year": this.form.selectedModel,
          "condition": this.form.condition,
          "color": this.form.color,
          "price_min": Number(this.form.vehiclePriceMin),
          "price_max": Number(this.form.vehiclePriceMax),
          "cylinders" : Number(this.form.cylinders),
          "transmission": this.form.transmission,
          "search": this.form.filter
        }
        this.currentPage = 1;
        this.getData(this.filters);
      },
      assingFilters() {
        if (localStorage.getItem('model')) {
          this.form.selectedModel = localStorage.getItem('model');
          // $(`#model${localStorage.getItem('model')}`).attr("checked", true).click(); 
          // $(`#moodel${localStorage.getItem('model')}`).prop("checked", true).trigger("click");
          // $(`#model${localStrage.getItem('model')}`).click();
          this.getBrands();
          if (localStorage.getItem('brand')) {
            this.form.selectedBrand = localStorage.getItem('brand');

            this.getVersions();
            if (localStorage.getItem('version')) {
              this.form.selectedVersion = localStorage.getItem('version');
            }
          }
        }

        this.filters = {
          "year": this.form.selectedModel,
          "brand": this.form.selectedBrand,
          "model": this.form.selectedVersion,
          "limit": 15,
        };
      },
      getData(filters) {
        var newData = [];
        this.$VehicleService.getVehicles(filters).then((result) => {
          this.data = result.data;
          let noPages = Math.ceil(result.total / 15);
          this.totalPages = noPages;

          this.getColors(this.data);
        }, (err) => {
          alert(err.message);
        });
      },
      getColors(data) {
        data.forEach(item => {
          if (this.colors.indexOf(this.capitalizeOnlyFirst(item.vehicle.color)) == -1) {
            this.colors.push(this.capitalizeOnlyFirst(item.vehicle.color));
          }
        });
      },
      capitalizeOnlyFirst(string) {
        return string.charAt(0).toUpperCase() + string.slice(1).toLowerCase();
      },
      filterImage(item) {
        if (item.length == 0 ) {
          return 'data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22286%22%20height%3D%22180%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20286%20180%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_172c887333d%20text%20%7B%20fill%3Argba(255%2C255%2C255%2C.75)%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A14pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_172c887333d%22%3E%3Crect%20width%3D%22286%22%20height%3D%22180%22%20fill%3D%22%23777%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%2298.91666793823242%22%20y%3D%2295.7%22%3EImage%20cap%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E';
        } else {
          let flag = false;
          let url = '';

          for( let i = 0 ; i < item.length ; i++ ) {
            if (item[i].url.includes('.jpg') || item[i].url.includes('.jpeg') || item[i].url.includes('.png') ) {
              flag = true;
              url = item[i].url;
            }
          }

          if (flag) {
            return url;
          }
          return 'data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%22286%22%20height%3D%22180%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20286%20180%22%20preserveAspectRatio%3D%22none%22%3E%3Cdefs%3E%3Cstyle%20type%3D%22text%2Fcss%22%3E%23holder_172c887333d%20text%20%7B%20fill%3Argba(255%2C255%2C255%2C.75)%3Bfont-weight%3Anormal%3Bfont-family%3AHelvetica%2C%20monospace%3Bfont-size%3A14pt%20%7D%20%3C%2Fstyle%3E%3C%2Fdefs%3E%3Cg%20id%3D%22holder_172c887333d%22%3E%3Crect%20width%3D%22286%22%20height%3D%22180%22%20fill%3D%22%23777%22%3E%3C%2Frect%3E%3Cg%3E%3Ctext%20x%3D%2298.91666793823242%22%20y%3D%2295.7%22%3EImage%20cap%3C%2Ftext%3E%3C%2Fg%3E%3C%2Fg%3E%3C%2Fsvg%3E';
        }
      },
      formatPrice(value) {
          let num = (value/1).toFixed(2);
          return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      },
    },
    created() {
      this.getModels();
      this.getStates();
      // this.refreshSelects();
    },
    mounted() {
      this.assingFilters();
      this.getData(this.filters);
    }
  }
</script>

