<template>
  <div>
    <vue-headful title="Credimvil | Crear cuenta"/>
    <menu-component v-bind:use-create-account="true"></menu-component>

    <!-- Banner Area -->
    <section class="banner_area banner_area_9">
        <div class="container">
            <h2>Crear cuenta</h2>
            <ol class="breadcrumb">
                <li></li>
                <li></li>
            </ol>
        </div>
    </section>
    <!-- Banner Area -->

    <!-- Easy cash Area -->
    <section class="easy_cash_area cot-credict crear-cuenta-form">
        <div class="container">
            <div class="row">
                <div class="col-lg-12 back-form">
                    <form class="from_main" action="php/contact.php" method="post" id="phpcontactform" novalidate="novalidate">
                        <div class="row">
                            <div class="col-lg-6 select_from">
                                <h6>Celular*</h6>
                                <input type="tel" class="post_select" id="phone" name="phone" v-model="phone" v-only-numbers :class="{ 'error': $v.phone.$invalid }">
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Correo electrnico*</h6>
                                <input type="email" class="post_select" id="email" name="email" v-model="email" :class="{ 'error': $v.email.$invalid }">
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Contrasea (Mnimo 12 caracteres)*</h6>
                                <input type="password" class="post_select" minlength="12" id="password" name="password" v-model="password" :class="{ 'error': $v.password.$invalid }">
                            </div>
                            <div class="col-lg-6 select_from">
                                <h6>Confirmar contrasea*</h6>
                                <input type="password" class="post_select" minlength="12" id="conf-password" name="conf-password" v-model="confPassword" :class="{ 'error': $v.confPassword.$invalid }">
                            </div>
                            <div class="col-12 select_from">
                                <input type="checkbox" name="condiciones" id="confirmation" v-model="confirmation"/><span for="condiciones">Estoy de acuerdo con el <a href="/privacy-notice">Aviso de privacidad</a></span>
                            </div>
                            <div class="form-group col-12">
                                <button class="theme_btn btn float-right" id="btn-send" v-on:click="register()">Crear cuenta</button>
                                <div id="js-contact-result" data-success-msg="Form submitted successfully." data-error-msg="Messages Successfully"></div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </section>
    <!-- End Easy cash Area -->
  </div>
</template>

<script>
  import { required, minLength, between, maxValue, minValue, email } from 'vuelidate/lib/validators';

  var regexEmail = /[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$/;
  export default {
    name: 'CreateAccount',
    data() {
      return {
        confirmation: false,
        email: '',
        password: '',
        confPassword: '',
        phone: '',
      }
    },
    validations() {
      return {
        email: { required, validateEmail: (value) => { return regexEmail.test(value); } },
        password: { required, minLength: minLength(12) },
        confPassword: { required, minLength: minLength(12) },
        phone: { required },
      }
    },
    methods: {
      register() {
        event.preventDefault();

        if (this.password.length < 12) 
          alert("El campo de contrasea tiene que ser de 12 o ms caracteres");
        else if (this.password != this.confPassword)
          alert("El campo de contrasea y confirmar contrasea no coinciden");
        else if (this.confirmation != true)
          alert("Por favor acepte el aviso de privacidad");
        else {
          const data = {
              email: this.email,
              password: this.password,
              phone: this.phone,
          };

          this.$SessionService.register(data).then((result) => {
            localStorage.setItem('session', result.data.session);
            location.reload();
          }, (err) => alert(err.msg));
        }
      }
    },
    created() {}
  }
</script>

<style lang="scss">
  .error {
    border-color: red !important;
  }
</style>



