                name: { required },
                email: { required, validateEmail: (value) => { return regexEmail.test(value); } },
                phone: { required, validatePhone: (value) => { return regexPhone.test(value); } },
                gender: { required },
                zipCode: { required },
                person: { required },
                // message: { required },
            }
        }
    },
    methods: {
        refreshSelects() {
            // $('#term').niceSelect('update');
            $('#birthday').datepicker({
                dateFormat: 'dd-mm-yy',
                changeMonth: true,
                changeYear: true,
                yearRange: "-100:+0"
            });
        },
        selectDate() {
            $('#birthday').datepicker('show');
        },
        money() {

        },
        onChangeTerm() {
            setTimeout(() => {
            this.refreshSelects();
                $('#term').on('change', () => {
                    this.formFinancing.term = Number($('#term').val());
                    // this.refreshSelects();
                });
            }, 500)
            
        },
        initRequest() {
            // this.formFinancing.monthlyFee = ((data.price - formFinancing.vehicleHitch) / formFinancing.term) + (((data.price - formFinancing.vehicleHitch) / 100) * 2.32) + 199 + 40;
            let data = {
                vehicle_price: Number(this.data.price),
                vehicle_hitch: Number(this.formFinancing.vehicleHitch),
                months: Number(this.formFinancing.term),
                vehicle_model: this.data.year.toString(),
                vehicle_brand: this.data.brand,
                vehicle_version: this.data.model,
                vehicle_description: this.data.publication.description
            };

            this.$UserService.createCreditQuotation({data: data}).then((result) => {
                alert('Se han mandado sus datos con xito!');
                window.location = '/pre-approval';
            }, (err) => alert(err.msg));
        },
        sendContact() {

            this.formContact.birthday = $('#birthday').val();

            if (!this.formContact.birthday) {
                alert('Favor de llenar los datos correspondientes');
                return;
            }

            this.formContact.gender = $('#gender').val();
            var arrayVehicles = [ this.data.id ];
            let data = {
                fullname: this.formContact.name,
                email: this.formContact.email,
                phone: this.formContact.phone,
                gender: this.formContact.gender,
                zipCode: this.formContact.zipCode,
                birthday: moment(this.formContact.birthday, 'DD-MM-YYYY').format('YYYY-MM-DD'),
                person: this.formContact.person,
                origin: this.formContact.origin,
                vehicles: arrayVehicles,
            };

            let id = 0;
            let dataInterest = {
                vehicleId: this.data.id,
                leadId: 0,
                status: 0,
                reason: 'Compra',
            };

            this.$VehicleService.newLead(data).then((result) => {
                id = result.data.id;
                dataInterest.leadId = id;
                this.$VehicleService.newInterest(this.data.id, id).then((result2) => {
                    this.$VehicleService.updateInterest(result2.data.id, dataInterest).then((result3) => {
                        alert('xito, enseguida un asesor se contactar con usted');
                        window.location = '/';
                    }, (err) => {
                        alert(err.message);
                    });
                }, (err) => {
                    alert(err.message);
                });
            },(err) => {
              alert(err.message);
            });
        },
        onOpen() {
            $('body').css({overflow: 'hidden'});
        },
        onClose() {
            $('body').css({overflow: 'auto'});
        },
        formatPrice(value) {
            let num = (value/1).toFixed(2);
            return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
        getData() {
            if (!localStorage.getItem('vehicleId')) {
                window.location = '/buy-car';
            }
            this.id = localStorage.getItem('vehicleId');

            this.$VehicleService.getVehicle(this.id).then((result) => {
                this.data = result.data;
                this.images = [];
                this.data.images.forEach(img => {
                    if (this.validateImage(img.url)) {
                        this.images.push(img.url);
                    } else {
                        this.videos.push(img.url);
                    }
                });
            }, (err) => {
                alert(err.message);
                window.location = '/buy-car';
            })
            
        },
        validateImage(item) {
            if (item.includes('.mp4')) {
                return false;
            } else {
                return true;
            }
        }
    },
    created() {
        this.getData();
        this.onChangeTerm();
        // this.refreshSelects();
    },

}
</script>

<style lang="scss">

    .error {
        border-color: red !important;
    }
    .post_input{
        position: relative;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: none;
        outline: none;
        font: 400 16px/40px "Source Sans Pro", sans-serif;
        padding: 0 25px;
        height: auto;
        border-radius: 5px;
        color: rgba(0, 0, 0, 0.6);
        min-width: 100%;
    }

    .post_select {
        position: relative;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: none;
        outline: none;
        font: 400 16px/40px "Source Sans Pro", sans-serif;
        padding: 0 25px;
        height: 44px;
        border-radius: 5px;
        color: rgba(0, 0, 0, 0.6);
        min-width: 100%;
    }

    .input-icons {
        width: 100%;
        margin-bottom: 0px;
        i {
            position: absolute;
            bottom: 4px;
            right: 8px;
            cursor: pointer;
        }
    }

    .hr-car {
        width: 100%;
        border-top: 2px solid #bfbfbf;
    }

    .vgs__container {
        top: 75px
    }
    .img-primary {
        width: 100%;
        height: 91vh;
        // object-fit: contain;
        cursor: pointer;
    }
    .img-secondary {
        width: 100%;
        padding-bottom: 10px;
        // object-fit: contain;
        cursor: pointer;
    }
    .img-none {
        display: none;
    }
    .tab {
        background-color: #2759ab;
        color: white;
        font-size: 20px;
        font-weight: 600;
        width: max-content;
        height: 40px;
        border-radius: 10px 10px 0px 0px;
    }
    .title-tab {
        color: white;
        font-size: 20px;
        font-weight: 600;
        padding: 8px 25px 10px 25px;
    }
    .hr-tab {
        border: 0px;
        border-top: 3px solid #2759ab;
        width: 100%;
        margin-top: 0px;
    }
    .detail {
        color: black;
        font-size: 18px;
    }
    .detail-title {
        color: rgba(0, 0, 0, 0.702);
    }
    .p-contact {
        font-size: 20px; 
        color: black;
        text-align: justify;
    }
    .img-contact {
        margin-right: 10px; 
        width: 25px;
    }
    .new_btn {
        .theme_btn {
            background: #f59019;
            &:before {
                background: #2759ab;
            }
        }
    }

    .modal-slideshow__container {
        top: 6rem !important;
    }
</style>


