import Vue from 'vue';

import VueRouter from 'vue-router';

import vueHeadful from 'vue-headful';

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

import Vuelidate from 'vuelidate';

import App from './App.vue';
import { routes } from './routes';

import Menu from './components/Menu.vue';
import onlyNumbers from './directives/onlyNumbers';
import InsuranceService from './services/Insurance';
import SessionService from './services/Session';
import UserService from './services/User';
import PolicyService from './services/Policy';
import VehicleService from './services/Vehicles';
import BusinessPartnersService from './services/BussinessPartners';
import CURPyRFC from './services/CalcularRFCyCURP';
import CopomexService from './services/Copomex';
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';

import Card from 'card';

Vue.$SessionService = SessionService;
Vue.$UserService = UserService;
Vue.$InsuranceService = InsuranceService;
VueRouter.$PolicyService = PolicyService;
VueRouter.$VehicleService = VehicleService;
VueRouter.$BusinessPartnersService = BusinessPartnersService;
VueRouter.$CopomexService = CopomexService;
VueRouter.$CURPyRFC = CURPyRFC;

Object.defineProperty(Vue.prototype, '$SessionService', {
    get() { return SessionService }
});
Object.defineProperty(Vue.prototype, '$UserService', {
    get() { return UserService }
});
Object.defineProperty(Vue.prototype, '$InsuranceService', {
    get() { return InsuranceService }
});
Object.defineProperty(Vue.prototype, '$PolicyService', {
    get() { return PolicyService }
});
Object.defineProperty(Vue.prototype, '$VehicleService', {
    get() { return VehicleService }
});
Object.defineProperty(Vue.prototype, '$BusinessPartnersService', {
    get() { return BusinessPartnersService }
});
Object.defineProperty(Vue.prototype, '$CopomexService', {
    get() { return CopomexService }
});
Object.defineProperty(Vue.prototype, '$CURPyRFC', {
    get() { return CURPyRFC }
});


Vue.use(VueSweetalert2);
Vue.use(VueRouter);
Vue.use(Vuelidate);
Vue.use(Card);
Vue.component('vue-headful', vueHeadful);
Vue.component('menu-component', Menu);
Vue.component('loading', Loading);

Vue.directive('only-numbers', onlyNumbers);

const router = new VueRouter({
    mode: 'history',
    routes: routes
});


router.beforeEach((to, from, next) => {
    const isAuthenticated = function() {
        const session = localStorage.getItem('session');

        return (session != undefined && session != '' && session != null)
    }

    switch (to.name) {
        case 'Login':
            if (!isAuthenticated())
                next();
            else
                next({ name: 'PreApproval' });
            break;
        case 'CreateAccount':
            if (!isAuthenticated())
                next();
            else
                next({ name: 'PreApproval' });
            break;
        case 'PreApproval':
            if (isAuthenticated())
                next();
            else
                next({ name: 'Login' });
            break;
        default:
            next();
            break;
    }
});

new Vue({
    router: router,
    render: h => h(App)
}).$mount('#app');


