import BusinessPartners from './views/BusinessPartners.vue';
import Contact from './views/Contact.vue';
import BuyCar from './views/BuyCar.vue';
import CarFilter from './views/CarFilter.vue';
import CreateAccount from './views/CreateAccount.vue';
import ViewCar from './views/ViewCar.vue';
import Faq from './views/Faq.vue';
import HelloWorld from './views/HelloWorld.vue';
import Home from './views/Home.vue';
import Login from './views/Login.vue';
import PreApproval from './views/PreApproval.vue';
import PrivacyNotice from './views/PrivacyNotice.vue';
import QuoteCredit from './views/QuoteCredit.vue';
import QuoteInsurance from './views/QuoteInsurance.vue';
import CreditRequest from './views/CreditRequest.vue';
import QuotationProcess from './views/QuotationProcess';
import TermsAndConditions from './views/TermsAndConditions';

export const routes = [
    { path: '/', name: 'Home', component: Home },
    { path: '/faq', name: 'Faq', component: Faq },
    { path: '/buy-car', name: 'BuyCar', component: BuyCar },
    { path: '/car-filter', name: 'CarFilter', component: CarFilter },
    // { path: '/view-car', name: 'ViewCar', component: ViewCar },
    { path: '/view-car', name: 'ViewCar', component: ViewCar },
    { path: '/contact', name: 'Contact', component: Contact },
    { path: '/privacy-notice', name: 'PrivacyNotice', component: PrivacyNotice },
    { path: '/terms-conditions', name: 'TermsAndConditions', component: TermsAndConditions },
    { path: '/quote-credit', name: 'QuoteCredit', component: QuoteCredit },
    { path: '/quote-insurance', name: 'QuoteInsurance', component: QuoteInsurance },
    { path: '/quotation-process', name: 'QuotationProcess', component: QuotationProcess },
    { path: '/create-account', name: 'CreateAccount', component: CreateAccount },
    { path: '/login', name: 'Login', component: Login },
    { path: '/business-partners', name: 'BussinessPartners', component: BusinessPartners },
    { path: '/pre-approval', name: 'PreApproval', component: PreApproval },
    { path: '/credit-request', name: 'CreditRequest', component: CreditRequest },
    { path: '*', redirect: '/' },
];


